@use 'base/mixins' as m;
@use 'base/variables' as v;
.dashboard-derecho {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 2rem;
    
    @media only screen and (min-width: 768px) {
      flex-direction: column;
    }
  }
  .dashboard {
    display: flex;
    flex-wrap: wrap;
  }
  
  .dashboard-izquierdo {
    flex-basis: 100%;
    min-width: 15rem;
    width: 15rem;
    background-color: #f2f2f2;
    @include m.tablet{
      flex-basis: 15%;

    }
    
  }
  
  .dashboard-derecho {
    flex-basis: 85%;
    flex-grow: 1;
    background-color: #fff;
  }
  @media only screen and (max-width: 768px) {
    .dashboard {
      flex-direction: column;
    }
    
    .dashboard-izquierdo {
      flex-basis: 100%;
    }
    
    .dashboard-derecho {
      flex-basis: 100%;
    }
  }
  .dashboard-izquierdo {
    padding: 20px;
    box-sizing: border-box;
  }
  
  .dashboard-derecho {
    padding: 20px;
    box-sizing: border-box;
    // border: 1px solid #e6e6e6;
    // border-radius: 4px;
    box-shadow: 0 2px 4px rgba(0,0,0,.1);
  }




  
 
  