@use "mixins" as m;
@use "variables" as v;
html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
    scroll-behavior: smooth;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    color: v.$negro;
}

.contenedor {
    width: 90%;
    max-width: 120rem;
    margin: 0 auto;
}

.flex-div-hz {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}

.flex-div-hz-nocenter {
    display: flex;
    gap: 1rem;
    align-items: center;
    justify-content: flex-start;
}

.icono {
    cursor: pointer;
}

.flex-div-vert {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 1rem;
}

.flex-div-vert-nocenter {
    display: flex;
    gap: 1rem;
    justify-content: flex-start;
    align-items: center;
}

.espacioBlanco {
    border: 0.1px solid #ffffff00;
    padding: 0.5rem;
}

.flex-espacio {
    justify-content: space-between;
}

.espacio-elementos {
    gap: 2rem;
}

.color-naranja {
    color: v.$naranja;
}

.color-negro {
    color: v.$negro;
}

.color-azulM {
    color: v.$azulM;
}

.bg-azulM {
    background-color: v.$azulM;
}
.bg-blanco {
    background-color: v.$blanco;
}
.bg-naranja{
    background-color: v.$naranja !important;
}
.bg-grisBajo {
    background-color: v.$grisBajoFuerte;
}

.color-grisBajo {
    color: v.$grisBajo;
}
.color-negro {
    color: v.$negro;
}
.shadow-naranja {
    box-shadow: v.$naranja 0px 5px;
}

.shadow-negro {
    box-shadow: v.$naranja 5px;
}

a {
    text-decoration: none;
    color: v.$blanco;
    padding: 0;
    margin: 0;
}

.centrar-texto {
    text-align: center;
}

.centrar-div {
    margin: 0 auto;
}

img {
    width: 100%;
}

.espacio {
    margin: 3rem;
}

.section {
    background-color: v.$bg-section;
    color: v.$naranja;
    padding: 0.5rem;
    border-radius: 2.5rem;
}

.ocultar {
    display: none !important;
}

.swal-wide {
    input {
        border: solid 1px v.$naranja;
        border-radius: 1rem;
    }
}

.letra-chica {
    font-size: 1rem;
}

.no-margin {
    margin: 0 !important;
}

.banner-naranja {
    padding: 1rem;
    color: v.$blanco;
    text-align: center;
    background-color: v.$naranja;
}

.clickeable {
    cursor: pointer;
}

.grid-column-3 {
    @include m.grid(2, 1rem);
    @include m.tablet {
        @include m.grid(3, 1rem);
    }
}

.grid-column-2 {
    @include m.grid(1, 1rem);
    @include m.tablet {
        @include m.grid(2, 1rem);
    }
}
.bannernuevo{
    margin: 1rem auto;
    font-family: v.$fuente_titulo !important;
    font-family: Saira, sans-serif !important;
    font-size: 2.5rem !important;
    font-weight: bold !important;
    text-shadow: 0.2rem 0.2rem 0.2rem black !important;
    box-shadow: rgb(0, 0, 0) 0px 1px 10px -5px !important;
    padding: 0.4rem !important;
    color: white !important;
    background-color: #ff6f0f !important;
    border-radius: 1rem !important;
    width: 80%;
    &.alert{
        background-color: #ffffff16 !important;
        font-size: 1.5rem !important;
    }
}