@use 'base/mixins' as m;
@use 'base/variables' as v;

  #signup:checked ~ .slider-tab{
    left: 50%;
  }
  #signup:checked ~ label.signup{
    color: #fff;
    cursor: default;
    user-select: none;
  }
  #signup:checked ~ label.login{
    color: #000;
  }
  #login:checked ~ label.signup{
    color: #000;
  }
  #login:checked ~ label.login{
    cursor: default;
    user-select: none;
  }

 
  
  .formulario-contenido .formulario .pass-link{
    margin-top: 5px;
  }
  .formulario-contenido .formulario .signup-link{
    text-align: center;
    margin-top: 30px;
  }
  .formulario-contenido .formulario .pass-link a,
  .formulario-contenido .formulario .signup-link a{
    color: v.$naranja;
    text-decoration: none;
  }
  .formulario-contenido .formulario .pass-link a:hover,
  .formulario-contenido .formulario .signup-link a:hover{
    text-decoration: underline;
  }
  