
/*Landing Page - Barra de Contenido*/
.a-noEffect{
    color: black;
}

/* CHECAR COMPATIBILIDAD PARA MEJORAR CÓDIGO -> Especificar "a" en las otras partes del código */
.a-noEffect:hover{
    color: black;
    cursor:context-menu;
}
.grid-contenedor-barCont {
    font-family: Saira, sans-serif;

    text-align: center;
    /*text-shadow: 0.1rem 0.1rem 0.1rem #000;*/

    /*border: solid black 0.1rem;*/

    width: 73%;
    margin: 0 auto;

    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(50px, 1fr));
    grid-gap: 10px;
}

.cell_barCont {
    /*border: solid black 0.1rem;*/
    align-items: center;
}

.cell1_barCont {
    grid-column: 1 / span 1;
    /*color: #ff6f0f;*/

    font-weight: bolder;
}

.cell2_barCont {
    grid-column: 2 / span 1;

    font-weight: 500;
}

.cell3_barCont {
    grid-column: 2 / span 1;

    font-weight: 500;

    display: none;
}
.a-barCont{
    transition: 0.35s;
    color: black;
}

.a-barCont:hover{
    transition: 0.35s;
    color: #ff6f0f;
    font-weight: 500;
}


.grid-contenedor-eventosLP {
    height: 30rem;
    margin: 0 auto;
    display: grid;
    justify-content: center; /* Centrar horizontalmente */
    align-items: center; 
}

.cell_eventosLP {
    /*border: solid black 0.1rem;*/
    
    display: flex;
    justify-content: center;
    align-items: center;

    border-radius: 2rem;

    width: 270px;
    height: 270px;

    background: #CCC;
    overflow: hidden;

    box-shadow: 0px 3px 10px 0px #000000;
}

.cell_eventosLP img{
    width: 100%;
    height: auto;
}

@supports(object-fit: cover){
    .cell_eventosLP img{
      height: 100%;
      object-fit: cover;
      object-position: top center;
    }
}

.cell1_eventosLP {
    grid-column: 1 / span 1;
    transition: 0.35s;
    /*color: #ff6f0f;*/
}

.cell2_eventosLP {
    grid-column: 2 / span 1;
    transition: 0.35s;
    /*color: #ff6f0f;*/
}

.cell3_eventosLP {
    grid-column: 3 / span 1;
    transition: 0.35s;
    /*color: #ff6f0f;*/
}
.cell4_eventosLP {
    grid-column: 4 / span 1;
    transition: 0.35s;
    /*color: #ff6f0f;*/
}
  
@media screen and (min-width: 200px) {
    .cell1_barCont {
        display: none;
        
    }

    .cell2_barCont {
        display: none;
    }

    .cell3_barCont {
        border: solid #ff6f0f 0.2rem;
        border-radius: 1rem;

        padding: 0.5rem;

        grid-column: 4 / 6;

        font-weight: bold;

        display: flex;
        justify-content: center;
        
        font-size: 1.8rem;
        transition: 0.35s;

        background-color: #ff6f0f;
        box-shadow: 1px 3px 20px -2px rgba(0,0,0,0.75);
        
    }
    
    .a-noEffect{
        color: white;
        transition: 0.35s;
    }
    
    .cell3_barCont:hover {
        border: solid #ff6f0f 0.2rem;
        transition: 0.35s;
        background-color: white;
        
    }

    .a-noEffect:hover{
        color: black;
        transition: 0.35s;
        cursor:context-menu;
    }
}

