@use 'base/mixins' as m;
@use 'base/variables' as v;

.swal-widegrande{
  width: 800px !important;
  height: 600px !important;
}
.swal2-radio, .swal2-checkbox {
  align-items: center;
  background: #fff;
  color: inherit;
  flex-direction: column !important;
}
.swal2-label{
  font-size: 1.5rem !important;
}
.swal2-label{
  font-size: 1.5rem !important;
}
.swal2-title{
  font-size: 2.3rem  !important;
}
.swal2-html-container{
  font-size: 1.5rem  !important;
}
.sw_button{
  border-radius: 1rem !important ;
  font-size: 2rem  !important;
    transition: all 0.5s ease-out !important;
    cursor: pointer !important;
    @include m.tablet(){
        padding: 1rem !important;
    }
    &.verde{
      background-color: v.$verde !important;
      color: v.$blanco!important; 
      border: 1px solid v.$verde!important;
      &:hover{
          color: v.$verde!important;
          background-color: v.$transparente!important;
      }
    }
    &.naranja{
      background-color: v.$naranja !important;
      color: v.$blanco!important; 
      border: 1px solid v.$naranja!important;
      &:hover{
          color: v.$naranja!important;
          background-color: v.$transparente!important;
      }
  }
  &.rojo{
    background-color: v.$rojo !important;
    color: v.$blanco!important; 
    border: 1px solid v.$rojo!important;
    &:hover{
        color: v.$rojo!important;
        background-color: v.$transparente!important;
    }
}
}
.swal2-validation-message{
  font-size: 1.2rem  !important;
}

.sw_selecc{
  font-size: 1.3rem  !important;
  border: 1px solid lightgrey  !important;
  border-bottom-width: 2px;
  font-size: 17px !important;
  transition: all 0.3s ease !important;
  &:focus{
    border-color: v.$naranja !important;
    
  }
  &::placeholder{
    color: #999;
    transition: all 0.3s ease !important;
  }
  &:focus::placeholder{
    color: #b3b3b3 !important;
  }
}

.validation-message{
  font-size: 1rem !important;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.69);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}
.overlay .btn-cerrar {
  width: 4rem;
  height: 4rem;
  background-color: rgb(230, 13, 13);
  color: #ffffff;
  border-radius: 50%;
  transition: background-color 0.5s;
  display: grid;
  place-items: center;
  font-weight: 700;
}
.overlay :hover {
  cursor: pointer;
}