@use 'variables' as v;
@use 'mixins' as m;

.swal {
  font-size: 2rem !important;
  color: v.$negro !important;
  padding: 1rem;
  @include m.tablet {
    width: 45rem;
    font-size: 4rem;
  }
  .swal2-loader {
    border-color: v.$naranja rgba(0, 0, 0, 0) v.$naranja rgba(0, 0, 0, 0) !important;
    /* Reemplaza "tu_color" con el color deseado */
  }

  .swal2-title {
    margin: 1rem auto;
    font-family: v.$fuente_titulo !important;
    font-family: Saira, sans-serif !important;
    font-size: 2.5rem !important;
    font-weight: bold !important;
    text-shadow: 0.2rem 0.2rem 0.2rem black !important;
    box-shadow: rgb(0, 0, 0) 0px 1px 10px -5px !important;
    padding: 0.4rem !important;
    color: white !important;
    background-color: #ff6f0f !important;
    border-radius: 1rem !important;
    width: 80%;

    @include m.tablet() {
      width: 60%;
    }
  }

  .swal2-html-container {
    font-family: v.$fuente_principal !important;

  }

  .swal2-confirm {
    background-color: v.$verde !important;
    color: v.$blanco !important;
    padding: 0.5rem;

  }

  .swal2-cancel {
    background-color: v.$rojo !important;
    color: v.$blanco !important;
    padding: 1rem !important;
  }


}
.swalalert {
  font-size: 2rem !important;
  color: v.$negro !important;
  width: 90%;

  @include m.tablet() {
    width: 35%;
  }
  .swal2-validation-message{
    background-color:lighten (v.$azulM)  !important;
    font-size: 1.35rem !important;
    color: v.$blanco !important;
  }
  .swal2-loader {
    border-color: v.$naranja rgba(0, 0, 0, 0) v.$naranja rgba(0, 0, 0, 0) !important;
    /* Reemplaza "tu_color" con el color deseado */
  }

  .swal2-confirm {
    background-color: v.$verde !important;
    color: v.$blanco !important;
    padding: 0.5rem;

  }

  .swal2-cancel {
    background-color: v.$rojo !important;
    color: v.$blanco !important;
    padding: 1rem;
  }


}
.swalgrande {
  font-size: 2rem !important;
  color: v.$negro !important;
  width: 90%;

  @include m.tablet {
    width: 60%;
    font-size: 4rem;
  }
  .swal2-loader {
    border-color: v.$naranja rgba(0, 0, 0, 0) v.$naranja rgba(0, 0, 0, 0) !important;
    /* Reemplaza "tu_color" con el color deseado */
  }

  .swal2-title {
    margin: 1rem auto;
    font-family: v.$fuente_titulo !important;
    font-family: Saira, sans-serif !important;
    font-size: 2.5rem !important;
    font-weight: bold !important;
    text-shadow: 0.2rem 0.2rem 0.2rem black !important;
    box-shadow: rgb(0, 0, 0) 0px 1px 10px -5px !important;
    padding: 0.4rem !important;
    color: white !important;
    background-color: #ff6f0f !important;
    border-radius: 1rem !important;
    width: 80%;

    @include m.tablet() {
      width: 60%;
    }
  }

  .swal2-html-container {
    font-family: v.$fuente_principal !important;
    max-height: 400px !important;
    overflow-y: scroll !important;
    img{
      width: 15rem !important;
    }
  }

  .swal2-confirm {
    background-color: v.$verde !important;
    color: v.$blanco !important;
    padding: 0.5rem;
  }

  .swal2-cancel {
    background-color: v.$rojo !important;
    color: v.$blanco !important;
    padding: 1rem;
  }


}