@use 'base/mixins' as m;
@use 'base/variables' as v;

.cards{
    margin: 1rem;
    .card{
        box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
        padding: 1.2rem;
        border-radius: 3rem;
        background-color: v.$blanco;
        max-height: 30rem;
        max-width: 25rem;
        min-height: 10rem;
        min-width: 8rem;
        font-size: 1rem;

        @include m.tablet{
            p{
                font-size: 1.5rem;
            }
        }
        img{
            width: 25%;
            
        }

    }
    &.card-auto{
        background-color: red !important;
        width: 100% !important; 
        height: 100% !important;
    }
}

