/** Desplegar Input-Select **/
.select-subeventos{
  margin: 0 auto;
  border-radius: 1rem;
  border: solid #FF6F0F 0.5rem;
  background-color: #FF6F0F;
  color: white;
  text-align: center;
  width: 95%;
  font-size: 1.6rem;
  cursor: pointer;
}

/* TITULO - Info. del Evento */
.title-main{
  font-family: Saira, sans-serif;
  font-size: 4rem;
  font-weight: bolder;

  display: flex;
  justify-content: left;
  align-items: center;

  padding: 1rem;
  padding-bottom: 2rem;

  margin: 0 auto;
  width: 90%;
}

.btn-uno {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 1rem;
  border: solid #ff6f0f 0.2rem;
  width: 90%;
  color: white;
  background: linear-gradient(to right, #ff6f0f 0%, #ff6f0f 50%, #ffffff 50%, #ffffff 100%);
  background-size: 200% 100%;
  transition: background-position 0.3s ease-in-out;
  cursor: pointer;

  padding: 0.8rem 2rem;
  font-size: 1.6rem;
}

.btn-uno:hover {
  background-position: -100% 0%;
  color: #ff6f0f;
}

.btn-dos {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 1rem;
  border: solid #ff6f0f 0.2rem;
  width: 90%;
  color: #FF6F0F;
  background: linear-gradient(to right, #ffffff 0%, #ffffff 50%, #ff6f0f 50%, #ff6f0f 100%);
  background-size: 200% 100%;
  transition: background-position 0.3s ease-in-out;
  cursor: pointer;

  padding: 0.8rem 2rem;
  font-size: 1.6rem;
  font-weight: bold;
}

.btn-dos:hover {
  background-position: -100% 0%;
  color: white;
}

.icon-color-orange{
  color: #FF6F0F;
}

.grid-container-mainEvento{
  font-family: Saira, sans-serif;
  /*width: 88%;*/
  margin: 0 auto;
	display: grid;
	/*grid-template-columns: repeat(auto-fit, minmax(50px, 1fr));*/
	/*border: solid black 0.1rem;*/
  border-radius: 2rem;
  /*box-shadow: 0px 2px 15px -6px #000000;*/
  box-shadow: 0px 0px 12px -3px #000000;
  /*padding: 1.5rem;*/
}

.grid-container-mainEvento:hover .cell1-mainEvento img{
  transform: scale(1.03);
  transition: 0.35s;
}

.cell-mainEvento{
  align-items: center;
  display: flex;
  justify-content: center;
  align-items: center;
  /*border: solid black 0.1rem;*/
}

.cell1-mainEvento{
  grid-column: 1 / span 5;
  grid-row: span 7;

  border-radius: 1rem;

  /*width: 40rem;
  height: 40rem;*/
}

.cell1-mainEvento img{
  object-fit: cover;
  border-radius: 2rem;
  /*width: 38rem;
  height: 38rem;*/
  /*box-shadow: 0px 2px 15px -6px #000000;*/
  box-shadow: 0px 0px 12px -3px #000000;
  transition: 0.35s;
}

.cell2-mainEvento{
  grid-column: 6 / span 11;
  /*font-size: 4rem;*/
  font-weight: bolder;
}

.info-mainEvento{
  text-align: center;
  /*font-size: 1.8rem;*/
  color: #757575;
}

.cell3-mainEvento{
  grid-column: 6 / span 4;
}

.cell4-mainEvento{
  grid-column: 10 / span 4;
}

.cell5-mainEvento{
  grid-column: 14 / span 3;
}

.cell6-mainEvento{
  grid-column: 6 / span 6;
}

.cell7-mainEvento{
  grid-column: 12 / span 5;
}

.cell8-mainEvento{
  grid-column: 6 / span 11;
  /*font-size: 1.8rem;*/
  /*padding: 0rem 1.5rem;*/
  text-align: justify;
}

.cell9-mainEvento{
  grid-column: 6 / span 5;
}

.cell13-mainEvento{
  grid-column: 11 / span 3;
}

.cell10-mainEvento{
  grid-column: 14 / span 3;
}

.cell11-mainEvento{
  grid-column: 6 / span 11;
}

.info-categoria{
  text-align: center;
  background-color: #FF6F0F;
  color: white;
  /*padding: 0.5rem;*/
  /*width: 95%;*/
  border-radius: 1rem;
  /*font-size: 1.8rem;*/
}

.cell12-mainEvento{
  grid-column: 6 / span 11;
  /*font-size: 2.8rem;*/
  font-weight: bolder;
  /*padding-left: 1.5rem;*/
  justify-content: left;
}

