@use 'base/mixins' as m;
@use 'base/variables' as v;
.intro {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    background: white;
    transition: 1s;
    font-family: "Zilla Slab", serif;
    img {
        top: 150px;
        position: static;
        width: 50%;
        @include m.tablet{
            width: 15%;
        }

    }
}



.intro p{
    color: #231F20;
    font-size: 1.6rem;
    margin-top: 10px;
    font-weight: bold;
}

.logo {
    font-size: 2.5rem;
    color: #FF4B2B;
}

.logo-parts {
    position: relative;
    display: inline-block;
    bottom: -20px;
    opacity: 0;
}

.logo-parts.active {
    bottom: 0;
    opacity: 1;
    transition: ease-in-out 0.5s;
}

.logo-parts.fade {
    bottom: 250px;
    opacity: 0;
    transition: ease-in-out 0.5s;
}

.content{
    width: 100%;
    padding: 50px;
    font-size: 2rem;
}