@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenedor {
    width: 100%;
    max-width: 120rem;
    margin: 0 auto;
}

.color-naranja {
    color: #ff6f0f;
}



.boton {
    border-radius: 1rem;
    transition: all .5s ease-out;
    cursor: pointer;
}

@media (min-width:768px) {
    .boton {
        padding: 1rem;
    }
}

.boton.naranja {
    background-color: #ff6f0f;
    color: #fff;
    border: 1px solid #ff6f0f;
}

.boton.naranja:hover {
    color: #ff6f0f;
    background-color: white;
}

.img-overlay {
    position: relative;
   // max-height: 35rem;
    min-height: 32rem;
    height:45rem;

    overflow: hidden;
    text-align: center;

    video {
        object-fit: cover;
        object-position: center;
      }
}

.img-overlay .overlay-index {
    position: absolute;
    background: rgba(0, 29, 35, .8);
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contenido-foto {
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.contenido-foto h2 {
    color: #fff;
    font-size: 6rem;
    margin: 0;
}

.contenido-foto p {
    font-family: "Open Sans", Arial, sans-serif;
    display: inline-block;
    padding: 1rem 2rem;
    font-weight: 550;
    font-size: 1.1rem;
    color: #fff;
    transition: font-size, transform 200ms;

}

.contenido-foto:hover h2 {
    transition: font-size .5s;
    font-size: 7rem;
}

