/* Index Calendar */
.indexCalendar{
  font-family: Saira, sans-serif;
  display: flex;
  align-items: center;
  justify-content: left;

  margin: 0 auto;
  font-weight: 600;

  width: 90%;
}
.indexCalendar div:nth-child(odd):hover {
  cursor: pointer;
  color: #ff6f0f;
  transition: 0.35s;
}
/*Primer Menú Index y Filtro*/
.grid-contenedor-indexFilter {
  font-family: Saira, sans-serif;

  text-align: center;

  /*border: solid black 0.1rem;*/
  border-radius: 1rem;
  box-shadow: 0px 0px 8px 1px rgba(0, 0, 0, 0.2);

  width: 90%;
  margin: 0 auto;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(50px, 1fr));
  /*grid-gap: 10px;*/
}

.cell_indexFilter {
  align-items: center;
  /*border: solid black 0.1rem;*/

  display: flex;
  align-items: center;
  justify-content: center;
}

.cell1_indexFilter {
  grid-column: 1 / span 1;
  font-weight: bold;
  cursor: pointer;
}

.cell2_indexFilter {
  grid-column: 2 / span 1;
  font-weight: 600;
}

.cell3_indexFilter {
  grid-column: 3 / span 1;
}

.cell4_indexFilter {
  grid-column: 4 / span 1;
}

/* Evento De Calendario */
.grid-contenedor-eventoCalendar {
  font-family: Saira, sans-serif;
  box-shadow: 0px 2px 15px -6px #000000;
  /*border: solid red 0.1rem;*/
  border-radius: 2.5rem;
  margin: 0 auto;

  width: 74%;
  /*padding: 1rem;*/

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(50px, 1fr));
  /*grid-gap: 10px;*/

  transition: 0.15s;

  position: relative;
}

.img-eventoCalendar {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 2rem;
  transition: transform 0.15s ease-in-out;
}

.grid-contenedor-eventoCalendar:hover {
  box-shadow: 0px 2px 11px 2px #FF6F0F;
  transition: 0.15s;
}
.grid-contenedor-eventoCalendar:hover .img-eventoCalendar{
  box-shadow: 0px 2px 11px 2px #FF6F0F;
  transform: scale(1.03);
  z-index: 1;
}
  
.cell_eventoCalendar {
  align-items: center;
  /*border: solid black 0.1rem;*/

  display: flex;
  align-items: center;
  justify-content: center;

  position: relative;
}

.cell1_eventoCalendar {
  grid-column: 1 / span 3;
  grid-row: span 4;
  /*border-radius: 1rem;*/
}

.cell2_eventoCalendar {
  grid-column: 4 / span 2;
}

.cell3_eventoCalendar {
  grid-column: 6 / span 4;
}

/*.cell4_eventoCalendar {
  grid-column: 8 / span 2;
}*/

.cell5_eventoCalendar {
  grid-column: 10 / span 2;
}

.cell6_eventoCalendar {
  grid-column: 4 / span 8;
  font-weight: bold;
  padding-top: 0.5rem;
}

.cell7_eventoCalendar {
  grid-column: 4 / span 8;
  color: #757575;
}

.cell8_eventoCalendar {
  grid-column: 4 / span 8;
}

.detBtnEC {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 1rem;
  border: solid #ff6f0f 0.2rem;
  width: 100%;
  color: white;
  background: linear-gradient(to bottom, #ff6f0f 0%, #ff6f0f 50%, #ffffff 50%, #ffffff 100%);
  background-size: 100% 200%;
  transition: background-position 0.3s ease-in-out;
  cursor: pointer;
}

.detBtnEC:hover {
  background-position: 0% -100%;
  color: #ff6f0f;
}

.eventoCalendarFH{
  font-weight: 600;
  color: #757575;
}

.eventoCalendarTipo{
  font-weight: 600;
  border-radius: 1.2rem;
  background-color: #D8D8D8;
  color: #757575;
}

/** Desplegar Input-Select **/
.custom-select-wrapper {
  position: relative;
  display: inline-block;
  user-select: none;
  box-shadow: rgb(0, 0, 0) 0px 1px 10px -5px;
}

.custom-select-wrapper select {
  display: none;
}

.custom-select {
  position: relative;
  display: inline-block;
}

.custom-select-trigger {
  position: relative;
  display: block;
  height: auto;
  /*padding: 0 84px 0 22px;*/
  font-weight: 500;
  color: #fff;
  background: #ff6f0f;
  border-radius: 0.8rem;
  cursor: pointer;
}

.custom-select-trigger:after {
  position: absolute;
  display: block;
  content: '';
  width: 8px;
  height: 8px;
  top: 50%;
  right: 10px;
  margin-top: -3px;
  border-bottom: 1px solid #fff;
  border-right: 1px solid #fff;
  transform: rotate(45deg) translateY(-50%);
  transition: all .4s ease-in-out;
  transform-origin: 50% 0;
}

.custom-select.opened .custom-select-trigger:after {
  margin-top: 3px;
  transform: rotate(-135deg) translateY(-50%);
}

.custom-options {
  z-index: 1;
  position: absolute;
  display: block;
  top: 100%;
  left: 0;
  right: 0;
  min-width: 100%;
  margin: 15px 0;
  border: 1px solid #b5b5b5;
  border-radius: 4px;
  box-sizing: border-box;
  box-shadow: 0 2px 1px rgba(0, 0, 0, .07);
  background: #fff;
  transition: all .4s ease-in-out;

  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(-15px);
}

.custom-select.opened .custom-options {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: translateY(0);
}

.custom-options:before {
  position: absolute;
  display: block;
  content: '';
  bottom: 100%;
  right: 25px;
  width: 7px;
  height: 7px;
  margin-bottom: -4px;
  border-top: 1px solid #b5b5b5;
  border-left: 1px solid #b5b5b5;
  background: #fff;
  transform: rotate(45deg);
  transition: all .4s ease-in-out;
}

.option-hover:before {
  background: #f9f9f9;
}

.custom-option {
  position: relative;
  display: block;
  padding: 0 22px;
  border-bottom: 1px solid #b5b5b5;
  font-weight: 600;
  color: #b5b5b5;
  cursor: pointer;
  transition: all .4s ease-in-out;
}

.custom-option:hover {
  color: white;
}

.custom-option:first-of-type {
  border-radius: 4px 4px 0 0;
}

.custom-option:last-of-type {
  border-bottom: 0;
  border-radius: 0 0 4px 4px;
}

.custom-option:hover,
.custom-option.selection {
  background: #ff6f0f;
}


/* ANIMACIÓN DROP DOWN */
.animatable {

  /* initially hide animatable objects */
  visibility: hidden;

  /* initially pause animatable objects their animations */
  -webkit-animation-play-state: paused;
  -moz-animation-play-state: paused;
  -ms-animation-play-state: paused;
  -o-animation-play-state: paused;
  animation-play-state: paused;
}

/* show objects being animated */
.animated {
  visibility: visible;

  -webkit-animation-fill-mode: both;
  -moz-animation-fill-mode: both;
  -ms-animation-fill-mode: both;
  -o-animation-fill-mode: both;
  animation-fill-mode: both;

  -webkit-animation-duration: 1s;
  -moz-animation-duration: 1s;
  -ms-animation-duration: 1s;
  -o-animation-duration: 1s;
  animation-duration: 1s;

  -webkit-animation-play-state: running;
  -moz-animation-play-state: running;
  -ms-animation-play-state: running;
  -o-animation-play-state: running;
  animation-play-state: running;
}

/* CSS Animations (extracted from http://glifo.uiparade.com/) */
@-webkit-keyframes fadeInDown {
  0% {
      opacity: 0;
      -webkit-transform: translateY(-20px);
  }

  100% {
      opacity: 1;
      -webkit-transform: translateY(0);
  }
}

@-moz-keyframes fadeInDown {
  0% {
      opacity: 0;
      -moz-transform: translateY(-20px);
  }

  100% {
      opacity: 1;
      -moz-transform: translateY(0);
  }
}

@-o-keyframes fadeInDown {
  0% {
      opacity: 0;
      -o-transform: translateY(-20px);
  }

  100% {
      opacity: 1;
      -o-transform: translateY(0);
  }
}

@keyframes fadeInDown {
  0% {
      opacity: 0;
      transform: translateY(-20px);
  }

  100% {
      opacity: 1;
      transform: translateY(0);
  }
}

.animated.animationDelay {
  animation-delay: .4s;
  /*-webkit-animation-delay: .4s;*/
}

.animated.animationDelayMed {
  animation-delay: 1.2s;
  /*-webkit-animation-delay: 1.2s;*/
}

.animated.animationDelayLong {
  animation-delay: 1.6s;
  /*-webkit-animation-delay: 1.6s;*/
}

.animated.fadeInDown {
  -webkit-animation-name: fadeInDown;
  -moz-animation-name: fadeInDown;
  -o-animation-name: fadeInDown;
  animation-name: fadeInDown;
}


