  flex-wrap: wrap;

  margin: 0 auto;
  width: 100%;
}

.box-miniEventos {
  flex: 1 0 50%;
  flex-basis: 100%;
  box-sizing: border-box;
  /*border: 1px solid black;*/
  /*padding: 10px;*/

  display: flex;
  justify-content: center;
  align-items: center;

  padding: 1rem 0rem;
  /*padding: 0.5rem;*/
}

    /* EVENTO MINI CARD */
    .grid-contenedor-miniEvento {
      font-family: Saira, sans-serif;
    
      /*border: solid black 0.1rem;*/
      border-radius: 2rem;
      box-shadow: 0px 0px 19px -4px #000000;
    
      margin: 0 auto;

      display: grid;
      /*grid-gap: 10px;*/

      transition: 0.15s;
      cursor: pointer;

      width: 52rem;
      height: auto;
    }

    .grid-contenedor-miniEvento:hover {
      box-shadow: 0px 0px 19px 4px #FF6F0F;
      transition: 0.15s;
    }

    .cell_miniEvento {
      align-items: center;

      padding: 0.2rem;
      /*border: solid black 0.1rem;*/
    
      display: flex;
      align-items: center;
      justify-content: center;
    }
    
    .cell1_miniEvento {
      grid-column: 1 / span 2;
      /*width: 25rem;*/
      width: auto;
      height: 20rem;
    }

    .cell1_miniEvento img{
      border-radius: 2rem 2rem 0rem 0rem;
      width: 100%;
      height: 100%;
      object-fit: cover;
    } 

    .cell2_miniEvento {
      grid-column: 1 / span 2;
      padding: 1rem;
      background-color: #FF6F0F;
      color: white;
      font-size: 1.8rem;
      text-align: center;
      font-weight: bold;
      transition: 0.35s;
    }

    .miniEventoFH{
      font-size: 1.2rem;
      color: white;
      /*background-color: #969696;*/
      background-color: #ABABAB;
      border-top: solid #000000 0.1rem;
    }

    .cell3_miniEvento {
      grid-column: 1 / span 1;
      border-right: solid black 0.1rem;
    }

    .cell4_miniEvento {
      grid-column: 2 / span 1;
    }

    .cell5_miniEvento {
      grid-column: 1 / span 2;
    }

    .cell6_miniEvento {
      grid-column: 1 / span 2;
      border-bottom: solid #000000 0.1rem;
    }

    .cell7_miniEvento {
      grid-column: 1 / span 2;
      font-size: 1.2rem;
      /*text-align: justify;*/
      text-align:center;
      padding: 0.5rem 1rem;
    }

    .cell8_miniEvento {
      grid-column: 1 / span 2;
    }
  
    .detBtnEC {
      font-size: 1.3rem;
      border-radius: 0rem 0rem 2rem 2rem;
      padding: 0.8rem;
    }

/** Desplegar Input-Select **/
.custom-select-trigger {
  width: 20rem;
  font-size: 1rem;
  line-height: 30px;
}

.custom-option {
  font-size: 1rem;
  line-height: 30px;
}
}
