        width: 22rem;
        height: 50rem;
        perspective: 800px;
        
        margin: 0 auto;
    }
    .card-front-mini-profile,
    .card-back-mini-profile {
        position: absolute;
        width: 100%;
        height: 100%;
        backface-visibility: hidden;
        transition: transform 0.6s;
    } 
    .card-front-mini-profile {
