@use 'base/mixins' as m;
@use 'base/variables' as v;

.nosotros{
    display: flex;
    flex-direction: column-reverse;
    @include m.tablet(){
        @include m.grid(2,2rem);

    }
    img{
        width: 32rem;
        box-shadow: 30px -8px v.$naranja;
        
    }
    .derecha{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 80%;
        margin: 0 auto;
    }
    .nota{
        padding: 1.5rem;
    }
}
