.overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.69);
}

.overlay picture {
  display: block;
  max-width: 80rem;
}

.overlay .btn-cerrar {
  width: 4rem;
  height: 4rem;
  background-color: rgb(230, 13, 13);
  color: #ffffff;
  border-radius: 50%;
  transition: background-color 0.5s;
  display: grid;
  place-items: center;
  font-weight: 700;
}

.overlay :hover {
