header {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-around;
  text-align: center;
  padding: 1.4rem;
}
header nav {
  display: flex;
}
header nav a {
  transition: color 200ms;
  padding: 1rem;
  color: #ffffff;
}
header nav a:last-child {
  border: 1.5px solid #FF6F0F;
  border-radius: 1rem;
}
header nav a:hover {
  color: #FF6F0F;
}

.menu-container {
  position: relative;
}

.menu-toggle {
  display: block;
  background-color: #001D23;
  color: #fff;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .menu-toggle {
    display: none;
  }
}

.menu {
  position: absolute;
  top: 100%;
  left: 0;
  display: none;
  padding: 10px;
  z-index: 1;
  margin: 0;
}
.menu a {
  display: block;
  padding: 5px 0;
}
@media (min-width: 768px) {
  .menu {
    position: static;
    display: flex;
    justify-content: flex-end;
    margin-top: 0;
  }
  .menu a {
    padding: 0 20px;
    color: #ffffff;
  }
}

.menu-toggle.active + .menu {
  display: block;
}
@media (min-width: 768px) {
  .menu-toggle.active + .menu {
    display: none;
  }
}

