.card-square {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 0%);
  width: 320px;
  height: 320px;
  background: #000;
  filter: drop-shadow(5px 5px 5px black);
}

.card-square .image {
  object-fit: cover;
  width: 100%;
  height: 100%;
  /*height: auto;*/
  overflow: hidden;
}

.card-square .image img {
  width: 100%;
  transition: 0.5s;
}

.card-square:hover .image img {
  opacity: 0.5;
  transform: translateX(30%); /*100%*/
}

.card-square .details {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%; /*100%*/
  height: 100%;
  background: #FFFFFF;
  transition: 0.5s;
  transform-origin: left;
  transform: perspective(2000px) rotateY(-90deg);
}

.card-square:hover .details {
  transform: perspective(2000px) rotateY(0deg);
}

.card-square .details .center {
  padding: 20px;
  text-align: center;
  background: #fff;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.card-square .details .center h1 {
  margin: 0;
  padding: 0;
  color: #ff6f0f;
  line-height: 20px;
  font-size: 15px;
  text-transform: uppercase;
}

.card-square .details .center h1 span {
  font-size: 14px;
  color: #262626;
}

.card-square .details .center p {
  font-family: "Open Sans", Arial, sans-serif;
  font-weight: lighter;
  margin: 10px 0;
  padding: 0;
  color: #262626;
}

.card-square .details .center ul {
  margin: 10px auto 0;
  padding: 0;
  display: table;
}

.card-square .details .center ul li {
  list-style: none;
  margin: 0 5px;
  float: left;
}

.card-square .details .center ul li a {
  border-radius: 1rem;
  display: block;
  background: #001d23;
  color: #fff;
  width: 10rem;
  height: 30px;
  line-height: 30px;
  text-align: center;
  transform: 0.5s;
}

.card-square .details .center ul li a:hover {
  background: #FFFFFF;
}

#contenedor {
  padding: 0%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  font-size: 15px;
}

#contenedor > div {
  width: 50%;
}

.prox-eventos {
  background: #ff6f0f;
  display: flex;
  justify-content: center;
  align-items: center;
  max-height: 7rem;
  min-height: 7rem;
  font-family: Saira, sans-serif;
  font-weight: 700;
}

.prox-eventos .texto-eventos {
  color: white;
  font-size: 3rem;
  transition: color 0.5s;
}

.prox-eventos .texto-eventos:hover {
  color: #000;
}

.seccion-eventos {
  background: white;
  height: 20rem;
  width: 100%;
  justify-content: center;
  align-items: center;
  max-height: 20rem;
  min-height: 10rem;
  font-family: Saira, sans-serif;
  /*font-size: 4rem;*/
  /*font-weight: 700;*/
}

.seccion-eventos .seccion-cards {
  margin: auto;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
  justify-content: center;
  align-items: center;
  width: 80%;
}

@media (min-width: 768px) {
  .seccion-eventos .seccion-cards {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .seccion-eventos .seccion-cards {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.seccion-eventos .seccion-cards .card-eventos {
  border: 0.8rem solid transparent;
  background: #fff;
  text-align: center;
  align-items: center;
}

