/* Index Calendar */
.indexCalendar {
  font-family: Saira, sans-serif;
  display: flex;
  align-items: center;
  justify-content: left;
  margin: 0 auto;
  font-weight: 600;
  width: 90%;
}

.indexCalendar div:nth-child(odd):hover {
  cursor: pointer;
  color: #ff6f0f;
  transition: 0.35s;
}

/*Primer Menú Index y Filtro*/
.grid-contenedor-indexFilter {
  font-family: Saira, sans-serif;
  text-align: center;
  /*border: solid black 0.1rem;*/
  border-radius: 1rem;
  box-shadow: 0px 0px 8px 1px rgba(0, 0, 0, 0.2);
  width: 90%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(50px, 1fr));
  /*grid-gap: 10px;*/
}

.cell_indexFilter {
  align-items: center;
  /*border: solid black 0.1rem;*/
  display: flex;
  align-items: center;
  justify-content: center;
}

.cell1_indexFilter {
  grid-column: 1/span 1;
  font-weight: bold;
  cursor: pointer;
}

.cell2_indexFilter {
  grid-column: 2/span 1;
  font-weight: 600;
}

.cell3_indexFilter {
  grid-column: 3/span 1;
}

.cell4_indexFilter {
  grid-column: 4/span 1;
}

/* Evento De Calendario */
.grid-contenedor-eventoCalendar {
  font-family: Saira, sans-serif;
  box-shadow: 0px 2px 15px -6px #000000;
  /*border: solid red 0.1rem;*/
  border-radius: 2.5rem;
  margin: 0 auto;
  width: 74%;
  /*padding: 1rem;*/
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(50px, 1fr));
  /*grid-gap: 10px;*/
  transition: 0.15s;
  position: relative;
}

.img-eventoCalendar {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 2rem;
  transition: transform 0.15s ease-in-out;
}

.grid-contenedor-eventoCalendar:hover {
  box-shadow: 0px 2px 11px 2px #FF6F0F;
  transition: 0.15s;
}

.grid-contenedor-eventoCalendar:hover .img-eventoCalendar {
  box-shadow: 0px 2px 11px 2px #FF6F0F;
  transform: scale(1.03);
  z-index: 1;
}

.cell_eventoCalendar {
  align-items: center;
  /*border: solid black 0.1rem;*/
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.cell1_eventoCalendar {
  grid-column: 1/span 3;
  grid-row: span 4;
  /*border-radius: 1rem;*/
