/* TITULO - Info. del Evento */
.title-main {
  font-family: Saira, sans-serif;
  font-size: 4rem;
  font-weight: bolder;
  display: flex;
  justify-content: left;
  align-items: center;
  padding: 1rem;
  padding-bottom: 2rem;
  margin: 0 auto;
  width: 90%;
}

.btn-uno {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 1rem;
  border: solid #ff6f0f 0.2rem;
  width: 90%;
  color: white;
  background: linear-gradient(to right, #ff6f0f 0%, #ff6f0f 50%, #ffffff 50%, #ffffff 100%);
  background-size: 200% 100%;
  transition: background-position 0.3s ease-in-out;
  cursor: pointer;
  padding: 0.8rem 2rem;
  font-size: 1.6rem;
}

.btn-uno:hover {
  background-position: -100% 0%;
  color: #ff6f0f;
}

.btn-dos {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 1rem;
  border: solid #ff6f0f 0.2rem;
  width: 90%;
  color: #FF6F0F;
  background: linear-gradient(to right, #ffffff 0%, #ffffff 50%, #ff6f0f 50%, #ff6f0f 100%);
  background-size: 200% 100%;
  transition: background-position 0.3s ease-in-out;
  cursor: pointer;
  padding: 0.8rem 2rem;
  font-size: 1.6rem;
  font-weight: bold;
}

.btn-dos:hover {
  background-position: -100% 0%;
  color: white;
}

.icon-color-orange {
  color: #FF6F0F;
}

