.intro-user-profile {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: white;
  transition: 0.5s;
}

.intro-user-profile img {
  top: 250px;
  position: absolute;
}

/*MODAL BOX*/
.trigger-user-profile {
  color: white;
  /*border: 2px solid rgb(216, 2, 134);*/
  border-radius: 1rem;
  /*padding: 18px 36px;*/
  /*padding: 0.8rem 1.2rem;*/
  /*display: inline-block;*/
  /*font-size: 1.7rem;*/
  /*letter-spacing: 1px;*/
  cursor: pointer;
  box-shadow: inset 0 100px 0 0 #001d23;
  -webkit-transition: ease-out 0.4s;
  -moz-transition: ease-out 0.4s;
  transition: ease-out 0.4s;
}

.trigger-user-profile:hover {
  box-shadow: inset 0 100px 0 0 #ff6f0f;
}

.modal-user-profile {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  transform: scale(1.1);
  transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s;
}

.modal-content-user-profile {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 1rem 1.5rem;
  width: 38rem;
  height: 55rem;
  border-radius: 1rem;
}

.show-modal-user-profile {
  opacity: 1;
  visibility: visible;
  transform: scale(1);
  transition: visibility 0s linear 0s, opacity 0.25s 0s, transform 0.25s;
}

