.button-burger-LP {
	border: solid #ff6f0f 0.1rem;
	border-radius: 0.5rem;
	padding: 1rem;
	font-size: 2rem;
	color: white;
	background-color: rgba(255, 111, 15, 0.8);
	display: none;
	transition: 0.35s;
}

.button-burger-LP:hover {
	background-color: rgba(255, 111, 15, 1);
}

.button-burger-LPC {
	border: solid #ff6f0f 0.1rem;
	border-radius: 0.5rem;
	padding: 0.9rem;
	font-size: 2rem;
	color: white;
	background-color: rgba(255, 111, 15, 1);
	display: none;
	transition: 0.35s;
}

.video-container {
	position: relative;
	width: 100%;
	height: 100%;
}

.video-container video {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.grid-contenedor-navbarCalendar {
	position: relative;
	z-index: 1;
	font-family: Saira, sans-serif;
	text-align: center;
	width: auto;
	height: 28rem;
	margin: 0 auto;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(50px, 1fr));
	background: rgba(10, 25, 25, 0.6);
	border-bottom: solid #ff6f0f 1rem;
}

.grid-contenedor-navbarCalendar:hover .cell1_navbarCalendar,
.grid-contenedor-navbarCalendar:hover .cell2_navbarCalendar {
	background: rgba(10, 25, 25, 0.8);
	transition: 0.35s;
}

.grid-contenedor-navbarCalendar:hover .cell3_TitleCalendar {
	/*font-size: 6.8rem;*/
	transition: 0.35s;
}

.cell_navbarCalendar {
	align-items: center;
	/*border: solid black 0.1rem;*/
}

.cell1_navbarCalendar {
	grid-column: 1 / span 1;
	display: flex;
	align-items: center;
	justify-content: center;
	/*padding-left: 10rem;*/
	transition: 0.35s;
	font-weight: bolder;
	font-size: 4rem;
	height: 8.5rem;
	text-shadow: 0.2rem 0.2rem 0.2rem black;
	z-index: 1;
    cursor: pointer;
}

.cell1_white {
	color: white;
}

.cell1_orange {
	color: #ff6f0f;
}

.cell2_navbarCalendar {
	grid-column: 2 / span 3;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	transition: 0.35s;
	height: 8.5rem;
	text-shadow: 0.2rem 0.2rem 0.2rem black;
}

.cell2_menuIndex {
	color: white;
	/*font-size: 1.8rem;*/
	transition: 0.35s;
	border-radius: 1rem;
	display: flex;
	justify-content: center;
	align-items: center;
}

.cell2_menuIndex:hover {
	color: white;
	font-weight: 500;
	border-radius: 1rem;
	transition: 0.35s;
	background-color: rgba(255, 111, 15, 1);
}

.cell2_menuIndexLogin {
	color: white;
	/*font-size: 1.8rem;*/
	transition: 0.35s;
	border-radius: 1rem;
	background-color: rgba(255, 111, 15, 0.8);
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
}

.cell2_menuIndexLogin:hover {
	color: white;
	font-weight: 500;
	border-radius: 1rem;
	background-color: rgba(255, 111, 15, 0.9);
	transition: 0.35s;
}

.cell_infoMenuCalendar {
	grid-column: 1 / span 4;
	padding: 0.5rem;
	z-index: 1;
	justify-content: left;
	align-items: center;
	background: rgba(10, 25, 25, 0.8);
	transform: translate(0%, 0px);
	display: none;
}


@keyframes slideCalendar {
	0% {
		transform: translate(100%, 0px);
		opacity: 0;
	}
	100% {
		transform: translate(0%, 0px);
		opacity: 1;
	}
}

@keyframes slideOutCalendar {
	0% {
		transform: translate(0%, 0px);
		opacity: 1;
	}
	100% {
		transform: translate(100%, 0px);
		opacity: 0;
	}
}

.cell3_navbarCalendar {
	grid-column: 1 / span 4;
	display: block;
	justify-content: center;
	align-items: center;
	margin: 0 auto;
	width: fit-content;
	height: 5rem;

	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.cell3_TitleCalendar {
	padding: 0.5rem 0rem 1rem 0rem;
	font-weight: bolder;
	/*font-size: 6rem;*/
	text-shadow: 0.3rem 0.3rem 1rem black;
	transition: 0.35s;
}

.cell3_Login {
	margin: 0 auto;
	width: fit-content;
	padding: 0.6rem 1.9rem 0.6rem 1.9rem;
	font-size: 2.1rem;
	font-weight: 600;
	color: white;
	border: solid #ff6f0f 0.3rem;
	background-color: #ff6f0f;
	border-radius: 1rem;
	text-shadow: 0.2rem 0.2rem 0.2rem black;
	transition: 0.35s;
}

.cell3_Login:hover {
	color: white;
	border: solid #ff6f0f 0.3rem;
	background-color: transparent;
	transition: 0.35s;
}

