		color: white;
		padding: 0.5rem 1.5rem;
		transition: 0.35s;
	}

	.infoMenuCalendar:hover{
		font-weight: 500;
		background-color: rgba(255, 111, 15, 1);
		color: white;
		transition: 0.35s;
	}

	.button-burger-LPC{
		/*display: flex;*/
		justify-content: center;
		align-items: center;
		font-size: 1.2rem;
	}

	.cell3_TitleCalendar {
