@use 'mixins' as m;
@use 'variables' as v;
.boton{
    border-radius: 1rem;
    text-align: center;
    padding: 1rem;
    transition: all 0.5s ease-out;
    cursor: pointer;
    @include m.tablet(){
        padding: 1rem;
    }
    &.naranja{
        background-color: v.$naranja;
        color: v.$blanco;
        border: 1px solid v.$naranja;
        &:hover{
            color: v.$naranja;
            background-color: v.$transparente;
        }
    }
    &.blanco{
        background-color: v.$transparente;
        color: v.$naranja;
        border: 1px solid v.$naranja;
        &:hover{
            color: v.$blanco;
            background-color: v.$naranja;
        }
    }
    &.verde{
        background-color: v.$transparente;
        color: v.$verde;
        border: 1px solid v.$verde;
        &:hover{
            color: v.$blanco;
            background-color: v.$verde;
        }
        margin: 0;
    }
    &.rojo{
        background-color: v.$transparente;
        color: v.$rojo;
        border: 1px solid v.$rojo;
        &:hover{
            color: v.$blanco;
            background-color: v.$rojo;
        }
        margin: 0;
    }
    &.gris{
        background-color: v.$transparente;
        color: v.$negro;
        border: 1px solid gray;
        &:hover{
            color: v.$blanco;
            background-color: gray;
        }
        margin: 0;

    }
}