/*BOTONES*/
/*** ESTILOS BOTÓN SLIDE LEFT ***/
.ov-btn-slide-left {
    background: #fff; /* color de fondo */
    color: #ff6f0f; /* color de fuente */
    border: 2px solid #ff6f0f; /* tamaño y color de borde */
    padding: 12px 50px;
    border-radius: 1rem; /* redondear bordes */
    position: relative;
    z-index: 1;
    overflow: hidden;
    display: inline-block;
    font-weight: bold;
  }
  .ov-btn-slide-left:hover {
    color: #fff; /* color de fuente hover */
  }
  .ov-btn-slide-left::after {
    content: "";
    background: #ff6f0f; /* color de fondo hover */
    position: absolute;
    z-index: -1;
    padding: 12px 50px;
    display: block;
    top: 0;
    bottom: 0;
    left: -100%;
    right: 100%;
    -webkit-transition: all 0.35s;
    transition: all 0.35s;
  }
  .ov-btn-slide-left:hover::after {
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    -webkit-transition: all 0.35s;
    transition: all 0.35s;
  }

  /*CARD*/
  .cardn {
  border-radius: 16px;
  margin: 0 auto;
  width: 100%;
  max-width: 100%;
  min-height: 20rem;
  /*box-shadow: 0.5rem 0.5rem #ff6f0f*/
  box-shadow: 0px 0px 8px 4px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  background-image: url("https://sie.iest.edu.mx/IESTEventos/build/img/evento8.jpg");
  background-size: contain;
  background-repeat: no-repeat;
}

.info {
  position: relative;
  width: 100%;
  height: 25rem;
  background-color: #ff6f0f;
  transform: translateY(100%)
    translateY(-39px)
    translateZ(0);
  transition: transform 0.5s ease-out;
  transition: 0.5s;
}

.info:before {
  z-index: -1;
  display: block;
  position: absolute;
  content: ' ';
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-size: contain;
  opacity: 0.25;
  transform: translateY(-100%)
    translateY(88px)
    translateZ(0);
  transition: transform 0.5s ease-out;
}

.cardn:hover .info,
.cardn:hover .info:before {
  transform: translateY(0) translateZ(0);
}

.cardn:hover .info{
    background-color: white;
    transition: 0.5s;
}

.title {
  margin: 0;
  text-align: center;
  padding-top: 0.58rem;
  padding-bottom: 0.3rem;
  /*font-size: 40px;*/
  font-size: 1.5rem;
  line-height: 1;
  color: white;
  transition: 0.5s;
}

.cardn:hover .title{
    background-color: #ff6f0f;
    /*color: black;*/
    transition: 0.5s;
    padding-top: 1.4rem;
    padding-bottom: 1.4rem;
}
