.button-burger-LP {
	border: solid #ff6f0f 0.1rem;
	border-radius: 0.5rem;
	padding: 1rem;
	font-size: 2rem;
	color: white;
	background-color: rgba(255, 111, 15, 0.8);
	display: none;
	transition: 0.35s;
}

.button-burger-LP:hover {
	background-color: rgba(255, 111, 15, 1);
}

.button-burger-LPC {
	border: solid #ff6f0f 0.1rem;
	border-radius: 0.5rem;
	padding: 0.9rem;
	font-size: 2rem;
	color: white;
	background-color: rgba(255, 111, 15, 1);
	display: none;
	transition: 0.35s;
}

.grid-contenedor-navbarSUGeneral {
	position: relative;
	z-index: 1;
	font-family: Saira, sans-serif;
	text-align: center;
	width: auto;
	margin: 0 auto;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(50px, 1fr));
	background: rgba(10, 25, 25, 1);
	/*border-bottom: solid #ff6f0f 1rem;*/
}

.cell_navbarSUGeneral {
	align-items: center;
	/*border: solid black 0.1rem;*/
}

.cell1_navbarSUGeneral {
	grid-column: 1 / span 1;
    height: 9rem;
	display: flex;
	align-items: center;
	justify-content: center;
	/*padding-left: 10rem;*/
	transition: 0.35s;
	font-weight: bolder;
	font-size: 4rem;
	text-shadow: 0.2rem 0.2rem 0.2rem black;
	z-index: 1;
	cursor: pointer;
}

.cell1_white {
	color: white;
}

.cell1_orange {
	color: #ff6f0f;
}

.cell2_navbarSUGeneral {
	grid-column: 2 / span 3;
    height: 9rem;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	transition: 0.35s;
	text-shadow: 0.2rem 0.2rem 0.2rem black;
}

.cell2_menuIndex {
	color: white;
	/*font-size: 1.8rem;*/
	transition: 0.35s;
	border-radius: 1rem;
	display: flex;
	justify-content: center;
	align-items: center;
}

.cell2_menuIndex:hover {
	color: white;
	font-weight: 500;
	border-radius: 1rem;
	transition: 0.35s;
	background-color: rgba(255, 111, 15, 1);
}

.cell2_menuIndexLogin {
	color: white;
	/*font-size: 1.8rem;*/
	transition: 0.35s;
	border-radius: 1rem;
	background-color: rgba(255, 111, 15, 0.8);
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
}

.cell2_menuIndexLogin:hover {
	color: white;
	font-weight: 500;
	border-radius: 1rem;
	background-color: rgba(255, 111, 15, 0.9);
	transition: 0.35s;
}

.cell_infoMenuGeneral {
	grid-column: 1 / span 4;
	padding: 0.5rem;
	z-index: 1;
	justify-content: left;
	align-items: center;
	/*background: rgba(10, 25, 25, 0.8);*/
    background: rgba(10, 25, 25, 1);
	transform: translate(0%, 0px);
	display: none;
}


@keyframes slideGenral {
	0% {
		transform: translate(100%, 0px);
		opacity: 0;
	}
	100% {
		transform: translate(0%, 0px);
		opacity: 1;
	}
}

@keyframes slideOutGeneral {
	0% {
		transform: translate(0%, 0px);
		opacity: 1;
	}
	100% {
		transform: translate(100%, 0px);
		opacity: 0;
	}
}

@media screen and (min-width: 250px) {
	.cell1_navbarSUGeneral {
		grid-column: 1 / span 2;
		font-size: 2rem;
	}

	.cell2_navbarSUGeneral {
		grid-column: 3 / span 2;
		padding-right: 2.1rem;
	}

	.cell2_menuIndex,
	.cell2_menuIndexLogin {
		display: none;
	}

	.button-burger-LP{
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 1.2rem;
	}

	.cell_infoMenuGeneral{
		grid-row-gap: 0.8rem;
		height: 20rem;
		grid-template-columns: minmax(0, 1fr);
		text-shadow: 0.2rem 0.2rem 0.2rem black;
	}

	.infoMenuGeneral{
		display: flex;
		/*justify-content: left;*/
		justify-content: space-between;
		align-items: center;
		border-radius: 1rem;
		background-color: rgba(255, 111, 15, 0.6);
		color: white;
		padding: 0.5rem 1.5rem;
		transition: 0.35s;
	}

	.infoMenuGeneral:hover{
		font-weight: 500;
		background-color: rgba(255, 111, 15, 1);
		color: white;
		transition: 0.35s;
	}

	.button-burger-LPC{
		/*display: flex;*/
		justify-content: center;
		align-items: center;
		font-size: 1.2rem;
	}
}

