@use 'base/mixins' as m;
@use 'base/variables' as v;


.formulariogral {
  display: grid;
  @include m.grid(1,1rem);

  grid-gap: 1rem; /* Espacio entre columnas */
  @include m.tablet(){
    @include m.grid(2,1rem);
  }
  &.grid-uno{
    grid-template-columns: repeat(1fr,1rem) !important;
  }
}
.camporadios{
  display: flex;
  align-content: center;
  justify-content: center;
}
@media (max-width: 768px) {
  .formulariogral {
    grid-template-columns: 1fr; /* Una sola columna en pantalla pequeña */
  }
}

.campo {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
}

.campo label {
  margin-bottom: 0.5rem;
}

.campo input,
.campo textarea,
.campo select {
  padding: 0.5rem;
  border-radius: 0.25rem;
  border: 1px solid #ccc;
  font-size: 1rem;
  line-height: 1.5;
  // text-transform: capitalize;
}
.campo textarea{
  resize: none;
}


.campo {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;

}

.campo label {
  margin-bottom: 0.5rem;
}

.campo input,
.campo textarea,
.campo select {
  width: 100%;
  outline: none;
  padding-left: 15px;
  border-radius: 5px;
  border: 1px solid lightgrey;
  border-bottom-width: 2px;
  font-size: 17px;
  transition: all 0.3s ease;
  &:focus{
    border-color: v.$naranja;
  }
  &:hover{
    border-color: v.$naranja;
  }
  &::placeholder{
    color: #999;
    transition: all 0.3s ease;
  }
  &:focus::placeholder{
    color: #b3b3b3;
  }

  &.error{
    background-color: v.$rojo !important;
    color: v.$blanco;
  }
  &.exito{
    background-color: v.$verde !important;
    color: v.$blanco;
  }
  &.warning{
    background-color: v.$naranja !important;
    color: v.$blanco;
    &::placeholder{
      color: v.$blanco;
    }
  }
}

.content{
    display: grid;
    width: 100%;
    margin: 0 auto;
    place-items: center;
  
  }
  
  .wrapper{
      overflow: hidden;
      max-width: 390px;
      background: #fff;
      padding: 30px;
      border-radius: 5px;
      box-shadow: 0px 15px 20px rgba(0,0,0,0.1);
      text-align: center !important;
      input[type="radio"]{
        display: none;
      }
    }
  
  
    .wrapper .slide-controls{
      position: relative;
      display: flex;
      height: 50px;
      width: 100%;
      overflow: hidden;
      margin: 30px 0 10px 0;
      justify-content: space-between;
      border: 1px solid lightgrey;
      border-radius: 5px;
    }
    .slide-controls .slide{
      height: 100%;
      width: 100%;
      color: #fff;
      font-size: 18px;
      font-weight: 500;
      text-align: center;
      line-height: 48px;
      cursor: pointer;
      z-index: 1;
      transition: all 0.6s ease;
    }
    .slide-controls label.signup{
      color: #000;
    }
    .slide-controls .slider-tab{
      position: absolute;
      height: 100%;
      width: 100%;
      left: 0;
      z-index: 0;
      border-radius: 5px;
      background: -webkit-linear-gradient(left, v.$naranja, v.$naranja);
      transition: all 0.6s cubic-bezier(0.68,-0.55,0.265,1.55);
    }
    
    .wrapper .formulario-contenedor{
        width: 100%;
        overflow: hidden;
        
      }
      .formulario-contenedor .formulario-contenido{
        display: flex;
        width: 200%;
      }
      .formulario-contenedor .formulario-contenido .formulario{
        width: 50%;
        transition: all 0.6s cubic-bezier(0.68,-0.55,0.265,1.55);
      }
      .formulario-contenido .formulario .campo{
        height: 50px;
        width: 100%;
        margin-top: 20px;
      }
      .formulario-contenido .formulario .campo input{
        height: 100%;
        width: 100%;
        outline: none;
        padding-left: 15px;
        border-radius: 5px;
        border: 1px solid lightgrey;
        border-bottom-width: 2px;
        font-size: 17px;
        transition: all 0.3s ease;
        
      }
      .formulario-contenido .formulario .campo input:focus{
        border-color: v.$naranja;
      }
      .formulario-contenido .formulario .campo input::placeholder{
        color: #999;
        transition: all 0.3s ease;
      }
      .formLogin .campo input:focus::placeholder{
        color: #b3b3b3;
      }
      .formulario .btn{
        height: 50px;
        width: 100%;
        border-radius: 5px;
        position: relative;
        overflow: hidden;
        padding: 0;
        margin: 0;
      }
      .formulario .btn .btn-layer{
        height: 100%;
        width: 300%;
        position: absolute;
        left: -100%;
        background: -webkit-linear-gradient(right, v.$naranja, v.$naranja);
        border-radius: 5px;
        transition: all 0.4s ease;;
        
      }
      .formulario .btn:hover .btn-layer{
        left: 0;
      }
      .formulario .btn input[type="submit"]{
        height: 100%;
        width: 100%;
        z-index: 1;
        position: relative;
        background: none;
        border: none;
        color: #fff;
        padding-left: 0;
        border-radius: 5px;
        font-size: 20px;
        font-weight: 500;
        cursor: pointer;
        padding: 0;
        margin: 0;
      }
      #icon_iest{
        width: 45% !important;
      }

