@use 'base/mixins' as m;
@use 'base/variables' as v;

header{
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-around;
    text-align: center;
    padding: 1.4rem;
    nav{
        display: flex;
        a{
            transition: color 200ms;
            padding: 1rem;
            color: #ffffff;

            &:last-child{
                border: 1.5px solid v.$naranja;
                border-radius: 1rem;
            }
            &:hover{
                color:v.$naranja;
            }
            
        }
        
    }
    
}
.menu-container {
    position: relative;
  }
  
  .menu-toggle {
    display: block;
    background-color: v.$azulM;
    color: #fff;
    padding: 10px 20px;
    border: none;
    cursor: pointer;
  
    @media (min-width: 768px) {
      display: none;
    }
  }
  
  .menu {
    position: absolute;
    top: 100%;
    left: 0;
    display: none;
    padding: 10px;
    z-index: 1;
    margin: 0;
  
    a {
      display: block;
      padding: 5px 0;
    }
  
    @media (min-width: 768px) {
      position: static;
      display: flex;
      justify-content: flex-end;
      margin-top: 0;
  
      a {
        padding: 0 20px;
        color: #ffffff;

      }
    }
  }
  
  .menu-toggle.active + .menu {
    display: block;
  
    @media (min-width: 768px) {
      display: none;
    }
  }